.hamb-button {
  --hamb-size: 50px;
  height: var(--hamb-size);
  width: var(--hamb-size);
  cursor: pointer;
  border: unset;
  background-color: #0000;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  display: flex;
}

@media screen and (min-width: 64em) {
  .hamb-button {
    display: none;
  }
}

.hamb-button__span {
  width: 75%;
  height: 4px;
  background: #fff;
  border-radius: 3px;
  transition: all .3s;
  display: block;
  position: relative;
  box-shadow: 0 2px 10px #0000004d;
}

.hamb-button__span + .hamb-button__span {
  margin-top: 4px;
}

.active .hamb-button__span:nth-child(1) {
  animation: .4s forwards top;
}

.active .hamb-button__span:nth-child(2) {
  animation: .4s forwards scaled;
}

.active .hamb-button__span:nth-child(3) {
  animation: .4s forwards bottom;
}

.not-active .hamb-button__span:nth-child(1) {
  animation: .4s forwards top-2;
}

.not-active .hamb-button__span:nth-child(2) {
  animation: .4s forwards scaled-2;
}

.not-active .hamb-button__span:nth-child(3) {
  animation: .4s forwards bottom-2;
}

@keyframes top {
  0% {
    top: 0;
    transform: rotate(0);
  }

  50% {
    top: calc(var(--hamb-size) / 6);
    transform: rotate(0);
  }

  100% {
    top: calc(var(--hamb-size) / 6);
    transform: rotate(45deg);
  }
}

@keyframes top-2 {
  0% {
    top: calc(var(--hamb-size) / 6);
    transform: rotate(45deg);
  }

  50% {
    top: calc(var(--hamb-size) / 6);
    transform: rotate(0);
  }

  100% {
    top: 0;
    transform: rotate(0);
  }
}

@keyframes bottom {
  0% {
    bottom: 0;
    transform: rotate(0);
  }

  50% {
    bottom: calc(var(--hamb-size) / 6);
    transform: rotate(0);
  }

  100% {
    bottom: calc(var(--hamb-size) / 6);
    transform: rotate(135deg);
  }
}

@keyframes bottom-2 {
  0% {
    bottom: calc(var(--hamb-size) / 6);
    transform: rotate(135deg);
  }

  50% {
    bottom: calc(var(--hamb-size) / 6);
    transform: rotate(0);
  }

  100% {
    bottom: 0;
    transform: rotate(0);
  }
}

@keyframes scaled {
  50% {
    transform: scale(0);
  }

  100% {
    transform: scale(0);
  }
}

@keyframes scaled-2 {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

